// Generic component that parses markdown files into HTML 
<template>
    <div class="hidden">
        <slot></slot>
    </div>
    <div v-html="ssg"></div>
</template>

<script>
import { marked } from 'marked';

export default {
    name: 'Markdown',
    props: {
    },
    computed: {
        ssg() {
            let md = this.$slots.default()[0].children
            return marked.parse(md)
        }
    }
}
</script>

<style scoped>
.hidden {
    display: none;
}
</style>