<template>
  <!-- duplicate navbar to provide correct spacing -->
  <div class="navspacer"></div>
  <div class="navbar fixed">
    <header class="d-flex flex-wrap justify-content-around px-3">
      <!-- YAR logo and title -->
      <a href="/" class="d-flex align-items-end text-decoration-none">
        <img src="/YAR Logo Light.png" alt="YAR Logo" class="me-2" style="height: 4rem; width:4rem;">
        <span class="fs-1 orbitron">York Aerospace and Rocketry</span>
      </a>

      <!-- Nav links (auto-generated) -->
      <ul class="nav align-items-end" id="navbar">
        <li class="nav-item fs-3" v-for="(section, index) in sections" :key="index">
          <a :href="section.link" class="nav-link active nunito" aria-current="page">{{ section.title }}</a>
        </li>
      </ul>
    </header>
  </div>
</template>

<script>

export default {
  name: "Nav",
  props: ["content"],
  mounted() {
    window.addEventListener("resize", this.resizeSpacer);
    window.addEventListener("load", this.resizeSpacer);
    this.resizeSpacer();
  },
  updated() {
    this.resizeSpacer();
  },
  methods: {
    resizeSpacer() {
      let height = document.getElementsByClassName("navbar")[0].clientHeight;
      document.getElementsByClassName('navspacer')[0].style.height = height + "px";
    }
  },
  data() {
    return {
      sections: [
        {
          title: "About Us",
          link: "/#About",
        },
        {
          title: "Meet the Team",
          link: "/#Meet",
        },
        {
          title: "Sponsors",
          link: "/#Sponsors"
        },
        {
          title: "Contact us",
          link: "/#Contact",
        },
      ],
    };
  },
};
</script>

